& .types
    width: 100%


    & .item
        $radio-outline-sz: 3px
        $radio-outline: $radio-outline-sz solid black
        $radio-circle: 10px + 2 * $radio-outline-sz
        $radio-border-active: 2px + $radio-outline-sz solid white

        display: flex
        justify-content: flex-start
        align-items: center
        width: 100px

        padding-bottom: $item-padding
        padding-left: $std-hor-padding

        & label
            user-select: none
            margin-left: 10px
            font-weight: 500

        & .radio
            outline: $radio-outline
            outline-offset: -1 * $radio-outline-sz
            background-color: white
            border-radius: 50%
            box-sizing: border-box
            width: $radio-circle
            height: $radio-circle

            &.active
                border: $radio-border-active
                background-color: black
