.schedule
    border-radius: $schedule-radius
    width: $container-width
    height: $schedule-height

    background-color: darkgray

    & header
        display: flex
        justify-content: flex-start
        align-items: center

        height: $schedule-header
        width: 100%

        border-top-right-radius: $schedule-radius
        border-top-left-radius: $schedule-radius

        background-color: gray

        & .text
            margin-left: 24px

            font-size: 36px

    & .sidebar
        $item-padding: 10px
        height: calc( 100% - $schedule-header - 2*$item-padding )
        width: calc($container-width / 5)

        margin-top: $item-padding
        border-right: 1px solid black
        @import  '../components/_types'

    & .content
        height: calc(100% - $schedule-header)
        width: calc($container-width - $container-width / 5)
